import {
  email,
  required,
  minLength,
  maxLength,
} from "vuelidate/lib/validators";

export default {
  name: "SignInPage",
  data() {
    return {
      waiting: false,
      email: "",
      password: "",
      isPwd: false,
      errorMessage: null,
    };
  },
  validations: {
    email: {
      required,
      email,
      maxLength: maxLength(30),
    },
    password: {
      required,
      minLength: minLength(8),
      maxLength: maxLength(20),
    },
  },
  computed: {
    emailErrorMessage() {
      if (!this.$v.email.required) {
        return "Preencha seu email";
      } else if (!this.$v.email.email) {
        return "Por favor insira um email válido";
      }

      return "";
    },
    passwordErrorMessage() {
      if (!this.$v.email.required) {
        return "Informe sua senha";
      } else if (!this.$v.password.minLength) {
        return "Mínimo de 8 dígitos";
      } else if (!this.$v.password.maxLength) {
        return "Máximo de 15 dígitos";
      }

      return "";
    },
  },
  methods: {
    async submit() {
      this.$v.$touch();
      if (!this.$v.$anyError) {
        if (this.waiting) {
          this.$q.notify({
            message: "Por favor, aguarde.",
          });
          return;
        }
        this.waiting = true;
        await this.$store
          .dispatch("users/retrieveToken", {
            credentials: {
              email: this.email,
              password: this.password,
            },
          })
          .then((response) => {
            if (response.data.user.confirmToken !== null) {
              this.$q.notify({
                message: "É necessário confirmar o email",
                color: "Black",
              });
            } else {
              this.$router.push({ name: "Profile" });
            }
            this.email = "";
            this.password = "";
            this.waiting = false;
          })
          .catch((error) => {
            if (error.message === "Request failed with status code 400") {
              this.$q.notify({
                message: "Não encontramos uma conta com esse email",
                color: "Black",
              });
            }
            if (error.message === "Request failed with status code 401") {
              this.$q.notify({
                message: "Email ou senha inválidos",
                color: "Black",
              });
            }
            if (error.message === "timeout of 5000ms exceeded") {
              this.$q.notify({
                message: "Houve um Problema, tente novamente",
                color: "Black",
              });
            }
            this.waiting = false;
          });
      } else {
        this.$q.notify({
          message: "Por favor, preencha os campos corretamente.",
        });
      }
    },
  },
};
